  display: flex;\n    flex-direction: column;\n    border: 1px solid $accent;\n    padding: 1em;\n}\n\n.import-form label {\n    color: $accent;\n    font-size: 0.8em;\n}\n\n.file-input {\n    color: $accent;\n    padding: 1em;\n}\n\n\n.more-settings {\n    position: sticky;\n    top: 0;\n\n    display: flex;\n    align-items: center;\n    justify-content: flex-end;\n\n    width: 100%;\n    max-width: 600px;\n    height: 3em;\n    margin: auto;\n    padding-right: 0.4em;\n\n    background-color: transparent;\n}\n\n.more-btn {\n    width: 2rem;\n    height: 2rem;\n\n    outline: none;\n    border: none;\n\n    background-color: $light;\n\n    cursor: pointer;\n}\n\n.more-btn:focus,\n.more-btn:hover {\n    border-top: 1px solid $accent;\n}\n\n#more-icon {\n    pointer-events: none;\n}\n\n\n\n\n\n.list-name {\n    display: flex;\n    align-items: flex-end;\n    justify-content: flex-start;\n    margin-bottom: 1em;\n\n    width: 100%;\n}\n\n.list-name-text {\n    /* margin-bottom: 2rem; */\n    display: flex;\n    margin-right: 1em;\n\n    color: $accent;\n    font-size: 1.2em;\n    font-family: 'Merriweather', serif;\n    line-height: 1;\n\n}\n\n.list-name .delete-btn {\n    min-width: 2rem;\n    min-height: 2rem;\n}\n\n.lists {\n    display: flex;\n    align-items: center;\n    justify-content: flex-start;\n    list-style: none;\n    color: $dark;\n    cursor: pointer;\n    padding-bottom: 0.3em;\n    flex-wrap: wrap;\n\n}\n\n.list-to-choose {\n    margin: 0.5em;\n    opacity: 0.6;\n}\n\n.list-to-choose:hover,\n.list-to-choose:focus {\n    color: $focus;\n    opacity: 1;\n    outline: none;\n}\n\n.size {\n    margin-bottom: 0.5em;\n}\n\n.size-completed {\n    color: $grey;\n    opacity: 0.6;\n}\n\n.list {\n    display: flex;\n    flex-wrap: nowrap;\n    flex-direction: column;\n}\n\n.list-undone {\n    margin: 0 0 2em 0;\n    list-style: none;\n}\n\n.list-completed {\n    color: $grey;\n    opacity: 0.6;\n}\n\n.item {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n\n    margin-bottom: 1em;\n\n    background-color: transparent;\n    border-bottom: 1px dashed $accent;\n    cursor: pointer;\n}\n\n.completed {\n    text-decoration: line-through;\n    text-decoration-color: $grey;\n    -moz-text-decoration-color: $grey;\n}\n\n.item-text {\n    flex: 1;\n    pointer-events: none;\n}\n\n.delete-btn {\n    position: relative;\n\n    align-self: flex-end;\n    overflow: hidden;\n\n    height: 2rem;\n    width: 2rem;\n\n    color: rgba(0, 0, 0, 0);\n    opacity: 0.5;\n\n    text-indent: -1000px;\n    font-size: 0.1px;\n    cursor: pointer;\n}\n\n.delete-btn:focus {\n    opacity: 1;\n}\n\n.delete-btn:hover {\n    opacity: 1;\n}\n\n.delete-btn-icon {\n    position: absolute;\n    left: 0.1rem;\n    bottom: -0.2rem;\n\n    display: block;\n\n    color: $accent;\n    background-color: white;\n\n    text-indent: 0rem;\n    font-size: 1rem;\n    pointer-events: none;\n}\n\n.add-item-form-container {\n    position: fixed;\n    left: 0;\n    bottom: 0;\n\n    width: 100%;\n\n    background-color: transparent;\n}\n\n.choose-list-nav {\n    padding: 0;\n}\n\n.form {\n    position: relative;\n\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n\n    width: 100%;\n    max-width: 600px;\n    height: 4em;\n    padding: 0.2em 2.5em;\n    margin: auto;\n\n    background-color: $light;\n}\n\n\n.add-list-form {\n    width: 100%;\n    padding: 0;\n}\n\n.text-input {\n    flex: 1;\n    height: 2.5em;\n    font-size: 1em;\n    text-indent: 0.5em;\n    outline: none;\n}\n\n.input-label {\n    position: absolute;\n    text-indent: -2500px;\n    left: -2500px;\n    overflow: hidden;\n\n    height: 1px;\n    width: 1px;\n\n    font-size: 1px;\n}\n\n\n.add-btn {\n    position: relative;\n\n    width: 4em;\n    height: 2.5em;\n\n    color: transparent;\n    background-color: $grey;\n    box-shadow: none;\n    border: none;\n\n    font-size: 1em;\n    text-indent: -2000px;\n}\n\n.add-btn:focus {\n    background-color: $accent;\n    border: none;\n    outline: none;\n}\n\n.add-btn:hover {\n    cursor: pointer;\n}\n\n.text-input:focus+.add-btn {\n    background-color: $accent;\n\n}\n\n.text-input:hover {\n    cursor: pointer;\n\n}\n\n.add-icon {\n    position: absolute;\n    bottom: 0.1em;\n    left: -0.1em;\n\n    display: block;\n    width: 100%;\n    height: 100%;\n\n    color: $light;\n    border: none;\n    outline: none;\n\n    font-size: 2em;\n    text-indent: 0.2em;\n    pointer-events: none;\n}\n\n.hidden {\n    display: none;\n}\n\ninput::placeholder {\n    opacity: 0.3;\n    letter-spacing: 0.2em;\n\n}"],"sourceRoot":""}]);
